@import "./vars.scss";

._slide{
   .SlidImg{
       height: 100vh;
       background-size: cover;
       display: flex;
       flex-direction: column;
       justify-content: center;
       @media (max-width:900px) {
           height: 50vh;
           .quat{
               margin-left: 40px;
           }
       }
       .quat{
           font-size: 40px;
           color: white;
           font-weight: 900;
           margin-left: 80px;
            text-shadow: black 2px 2px 2px;
       }
   }
}

._Discr{
    background: $pri;
    padding: 50px 0px;
    height:max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media (max-width:900px) {
        .Disc{
            flex-direction: column;
            justify-content: center;
            text-align: center;
            i{
                
                display: block;
                text-align: center;
                svg{
                    text-align: center;
                    width: 100%;
                }
            }
        }
   }
    .Disc{
        display: flex;
        color:white;
        font-size: 25px;
        align-self: center;
        i{
            width: 100%;
            display: flex;
            font-size: 200px;
            flex-direction: column;
            justify-content: center;
            margin-right:50px ;
        }
        
       
        
        width: 70%;
        

    }
}
._cards{
    padding: 100px 0px;
    overflow: hidden;
    background-image:linear-gradient(#3301759a,#0777d3ad), url("../a.jpg");
    background-attachment: fixed;
    background-size: contain;
    background-position-x: center;
    @media (max-width:900px) {
        &>div{
            .card{
                flex-direction: column;
                justify-content: center;
                
                
                &>.head{
                    align-self: center;
                    width: 250px;
                    
                }
                &>.cont{
                    align-self: center;
                    width: 100% !important;
                    padding: 0 !important;
                    text-align: center;
                }
                
            }
        }

    }
    &>div{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
        justify-content: space-around;
        .card{
            padding: 80px 40px;
            width: 100%;
            display: flex;
           
            
            &:nth-child(even){
                justify-content: end;
            }
            &>.head{
                -webkit-mask-image: url(./a.svg);
                    mask-image: url(./a.svg);
                mask-repeat: no-repeat;
                background-size: cover;
                border-radius:30px;
                
                box-shadow:  20px 20px 60px #1a3873, 
                 -20px -20px 60px #244c9b;
                height: 300px;
                min-width: 300px;
                cursor: pointer;
                margin: 0 10px;
            }
            &>.cont{
                h1{
                    color: $pri;
                    font-weight: 900;
                    text-transform: uppercase;
                    margin: 10px 0;
                    text-decoration: underline;

                }
                width:40%;
                padding: 10px;
                color: white;
                font-weight:700;
                font-size: 20px;
                display: flex;
                flex-direction:column;
                justify-content: center;

                
            }
        }
        

    }
}
    
    


.ImgGal{
    background: white;
    padding: 50px 0px;
    h1{
        color: $pri;
    }
    ._img{
        height:350px;
        width: 300px;
        background: $f;
        cursor: pointer;
        background-position: center;
        background-size: cover;
    }
    button{
        margin-left: auto;
        margin-right: auto;
        margin-top: 70px;
        display:block;
    }
}

.footer{
    height: max-content;
    padding:10px 80px;
    color: white;
    ._head{
        font-weight: 900;
        font-size: 60px;
        color: $pri;
        margin-bottom: 20px;
    }
    .Info{
        &>div>span:nth-child(1){
            font-weight: 900;
        }
        &>div>span:nth-child(2){
            margin-left: 15px;
        }

    }
}