$pri:#071e3d;
$sec:#1f4287;
$th:#278ea5;
$f:#21e6c1;


body{
    background:$sec;
    font-family: roboto;
    margin: 0;
}
*{
    box-sizing: border-box;
}
._cont{
    margin-top: 60px;
}

h1{
    color:white;
}

button{
    padding:10px 15px;
    background:rgb(209, 208, 208);
    border:none;
    font-size: 18px;
    color: $pri;
    border-radius: 8px;
    cursor: pointer;
    transition: .3s;
    &:hover{
        background:$pri;
        color: white;
        transition: .5s;
        
    }
}