@import "./vars.scss";
._header{
    ._MobMenu{
        position: fixed;
        width: 100%;
        height: 100%;
        background:#242a33dc;
        left: 0;
        top: 0;
        &>div{
            &.rmv{
                animation: Rmv .4s;
            }
            width:200px;
            height: 100vh;
            background:white;
            overflow: auto;
            z-index: 1;
            animation: MenuBar .3s;
            display:flex;
            flex-direction: column;
            
            ._head{
                height: 120px;
                color:$pri;
                font-size: 30px;
                font-weight: 900;
                padding:30px;
            }

            ._Cont{

               
                ._Menu{
                    display: flex;
                    flex-direction: column;
                    color:$pri;
                    div{
                        margin:0px 0px;
                        padding:0px 20px;
                        line-height: 50px;
                        &:hover{
                            background:#071e3d7c;
                            &::after{
                                content: "";
                                position: absolute;
                                left: calc(100% - 5px);
                                bottom: 0;
                                width: 5px;
                                height: 100%;
                                border-radius: 10px 0px 0px 10px;
                                background: $pri;
                                transition: .5s;
                            }
                        }
                    }
                }
            }
            
        }
        @keyframes MenuBar {
            0%{
                transform: translateX(-250px);
            }
            100%{
                transform: translateX(00px);
            }
        }
        @keyframes Rmv {
            0%{
                transform: translateX(0px);
            }
            100%{
                transform: translateX(-250px);
            }
        }
    }
    .MenuBut{
        div>span{
            background: none;
            color: white;
            border: none;
            font-size:30px;
            position: absolute;
            left: 0;
            width: 50px;
            cursor: pointer;
            height: 60px;
            padding: 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            z-index: -1;
            //line-height: 60px;
            &:hover{
                background: $sec;
            }

        }
        @media (min-width:600px) {
            display: none;
        }
    }
    z-index: 100;
    background: $pri;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: white;
    display: flex;
    padding: 0 80px;
    line-height: 60px;
    justify-content: space-between;
    @media (max-width:600px) {
        padding: 0 20px;
        justify-content: left;
        ._Name{
            margin-left: 60px;
        }
    }
    ._Name{
        font-size: 30px;
        font-weight: 900;
    }
    ._Menu{
        @media (max-width:600px) {
            display: none;
        }
        display: flex;
        &>a{
            text-decoration: none;
            color: inherit;
        }
        &>a>div{
            margin:0 10px;
            cursor: pointer;
            svg{
                margin-right: 5px;
            }
            &:hover{
                font-weight: 900;
                transition: .3s;
                line-height:60px;
                position: relative;
                &::after{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 5px;
                    border-radius: 10px 10px 0 0;
                    background: $th;
                    transition: .5s;
                }
            }
        }
        
    }
}